import dots from "../../../assets/svg/dots-group.svg";
import rectangle from "../../../assets/svg/rectangle.svg";

import useMediaQuery from "../../../shared/useMediaQuery";

import "./style.scss";


const Aboute = () => {
    const matches = useMediaQuery("(max-width: 425px)");

    return (
        <section className="aboute" id = "aboute">
            <div className="aboute-content">
                <h1 className="aboute-header"><span className="hesteg" />  </h1>
                
                <div className="aboute-content">
                    <p>,  !</p>
                    <p> Fullstack   .      .     :</p>
                    <p>   ,    .        React, Next.js, Spring  Django.    PostgreSQL, MySQL, SQLite.    Linux  Docker.</p>
                    <p>  MayBe  vseTUT    ,     ,       .</p>
                </div>
            </div>


            {
                !matches && (
                    <div className="aboute-background">
                        <img src={dots} className="dots" />
                        <img src={rectangle} className="rectangle" />
                    </div>
                )
            }
        </section>
    )
}


export default Aboute;